import React, { useEffect } from "react";
import { Icon } from "@iconify/react";
import { useDispatch, useSelector } from "react-redux";
import { CardTitle, Table } from "reactstrap";
import { fetchEmployees, createEmpployee , deleteEmployee ,  fetchProfileRequest} from "../action/action";
import { Link } from "react-router-dom";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import { DatePicker } from "antd";
import {
  Button as BootstrapButton,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
} from "reactstrap";
const Buttons = () => {
  const [isOpen, setIsOpen] = useState(false);
  const [first, setFirst] = useState("");
  const [JoinDate, setJoinDate] = useState(null);
  const [endDate, setEndDate] = useState(null);
  const [last, setLast] = useState("");
  const [email, setEmail] = useState(null);
  const [showDeletePopup, setShowDeletePopup] = useState(false);

  const navigate = useNavigate();
  const dispatch = useDispatch();
  const timeOffData = useSelector((state) => state.login.employees);
  console.log(timeOffData);
  const check = useSelector((state) => state);
  console.log(check);
  console.log("Redux State:", timeOffData);
  useEffect(() => {
    dispatch(fetchEmployees());
  }, []);
  if (!timeOffData || timeOffData.length === 0) {
    return <div>Loading...</div>;
  }

  const toggleModal = () => {
    setIsOpen(!isOpen);
  };

  // Function to handle form submission
  const handleSubmit = () => {
    const payload = {
      FirstName: first,
      LastName: last,
      Email: email,
      JoinDate: JoinDate,
    };
    dispatch(createEmpployee(payload));
    console.log("fucntion called");
    toggleModal();
  };
  const handleDelete = (employeeId) => {
    console.log("close button called with emo",employeeId)
    dispatch({ type: "DELETE_EMPLOYEE", payload: employeeId });
    // Implement the delete functionality here
    // This function will be called when the "Delete" button in the popup is clicked
    setShowDeletePopup(false);
    // Add your delete logic here...
  };

  const handleClose = (employeeId) => {
  
    setShowDeletePopup(false);
  };
  const handleIconClick = (employeeId) => {
    setShowDeletePopup((prevState) => ({ ...prevState, [employeeId]: !prevState[employeeId] }));
    console.log(employeeId)
     
  };

  const handleNameClick = (employeeId) => {
    // Dispatch the action to fetch the profile data
    dispatch(fetchProfileRequest(employeeId));
    console.log(employeeId)
navigate("/employeeprofile", { state: employeeId })
    // Redirect to the profile page with the employeeId as a URL parameter
   
  };
  return (
    <>
      <div className="header">
        <div className="left-corner">
          <i className="user-icon bi bi-person-circle"></i>
          <div>Employees</div>
        </div>
        <div className="tabs">
          <div className="tabs-bar">
            <div className="tabs-nav-container">
              <span className="tabs-tab-prev">
                <span className="tabs-tab-prev-icon">
                  <i className="tabs-tab-prev-icon-target"></i>
                </span>
              </span>
              <span className="tabs-tab-next">
                <span className="tabs-tab-next-icon">
                  <i className="tabs-tab-next-icon-target"></i>
                </span>
              </span>
              <div className="tabs-nav-wrap">
                <div className="tabs-nav-scroll">
                  <div className="tabs-nav">
                    <div className="tabs-tab">
                      <div className="hidden-div">
                        <a
                          className="anchor"
                          href=""
                          style={{
                            lineHeight: "55px",
                            display: "inline-block",
                          }}
                        >
                          Manage Employee
                        </a>
                      </div>
                    </div>
                    <div className="tabs-tab">
                      <div className="hidden-div">
                        <Link
                         to='/directory'
                          style={{
                            lineHeight: "55px",
                            display: "inline-block",
                          }}
                        >
                          Directory
                        </Link>
                      </div>
                    </div>
                    <div className="tabs-tab">
                      <div className="hidden-div">
                        <a
                          href=""
                          style={{
                            lineHeight: "55px",
                            display: "inline-block",
                          }}
                        >
                          Org Chart
                        </a>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="mytimeoff" style={{ marginTop: "120px" }}>
        <div className="insidemytime">
          <div>
            <h6>Unpaid</h6>
            <span>30 days</span>
          </div>
<div style={{border:"0px", marginRight:'-25%'}}>
          <button className="timeoffbuttton" onClick={toggleModal}>
            New Profile
          </button></div>
        </div>
      </div>

      <div className="myattedance">
        <div></div>
        <div className="filtercombinaion">
          <div className="datepicker">
            <DatePicker
            style={{width:"190px"}}
              // value={startDate}
              // onChange={(date) => setStartDate(date)}
            />
            <DatePicker  style={{width:"190px"}} value={endDate} onChange={(date) => setEndDate(date)} />
          </div>

         
        </div>
        <Table className="no-wrap mt-3 align-middle " responsive borderless>
          <thead>
            <tr>
              <th>Employee Name</th>
              <th>Job Title</th>
              <th>Line Manager</th>
              <th>Department</th>
              <th>Office</th>
              <th>Employee Status</th>
              <th>Account</th>
              <th>
                <Icon icon="uil:setting" height={18} /> 
              </th>
            </tr>
          </thead>
          <tbody>
            {timeOffData &&
              timeOffData.map((tdata, index) => (
                <tr key={index} className="border-top">
                  <td className="mb-0">
                  <div className="d-flex align-items-center p-2" onClick={() => handleNameClick(tdata.employeeId)}>
                      <img
                        src={tdata.featuredImageURL}
                        className="rounded-circle"
                        alt="avatar"
                        width="45"
                        height="45"
                      />
                      <div className="ms-3"  >
                        <span className="mb-0">{tdata.fullName}</span>
                      
                      </div>
                    </div></td>
                  <td>{tdata.designationName}</td>
                  <td>{tdata.lineManagerName}</td>
                  <td>{tdata.departmentName}</td>
                  <td className="scrollable-td">{tdata.attachment}</td>
                  <td>
                    {tdata.employeeStatus}
                  </td>
                  <td></td>
                  <td>
                    <Icon icon="uil:ellipsis-h"   onClick={() => handleIconClick(tdata.employeeId)}/>
                  </td>
                </tr>
              ))}
          </tbody>
        </Table>
      </div>
      {/* <div className="myattedance">
        <div>
          <CardTitle tag="h6" className="border-bottom p-4 mt-0">
            <Icon
              icon="uil:file-edit-alt"
              color="#2ad25f"
              width={22}
              height={22}
            />
            <span style={{ marginLeft: "10px" }}>Balance History</span>
          </CardTitle>
        </div>

        <Table className="no-wrap mt-3 align-middle " responsive borderless>
          <thead>
            <tr>
              <th>Date</th>
              <th>Event</th>
              <th>Type</th>
              <th>Changed By</th>
              <th>Changed (days)</th>
            </tr>
          </thead>
          <tbody>
            {timeOffData &&
              timeOffData.map((tdata, index) => (
                <tr key={index} className="border-top">
                  <td className="mb-0">{tdata.date}</td>

                  <td>{tdata.clockInTime}</td>
                  <td>{tdata.clockInLocation}</td>
                  <td>{tdata.clockOutTime}</td>
                  <td className="scrollable-td">{tdata.clockOutLocation}</td>
                </tr>
              ))}
          </tbody>
        </Table>
      </div> */}
      <div  >
      <Modal isOpen={isOpen} toggle={toggleModal} className="requestmodal "  style={{ width: '700px' }}>
        <ModalHeader toggle={toggleModal} >New Profile</ModalHeader>
        <ModalBody className="requestbody">
          <div className="form-group">
          <div className="newprofile">
            <div >
              <div>
                <input
                  type="text"
                  className="form-control"
                  value={first}
                  onChange={(e) => setFirst(e.target.value)}
                  placeholder="First Name"
                />
                <input
                  placeholder="Last Name"
                  type="text"
                  className="form-control"
                  value={last}
                  onChange={(e) => setLast(e.target.value)}
                />
                <input
                  placeholder="Email"
                  type="text"
                  className="form-control"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                />
              </div>
            </div>
            <div className="form-group" style={{marginTop:"15px", marginLeft:"-16px"}}>
            
            <DatePicker style={{width:"106%"}} value={JoinDate}
              onChange={(dates) => {
                setJoinDate(dates);
                
              }} />
            </div>
          </div>
          </div>
        </ModalBody>
        <ModalFooter >
          <BootstrapButton color="primary" onClick={handleSubmit}>
            Submit
          </BootstrapButton>{" "}
          <BootstrapButton color="secondary" onClick={toggleModal}>
            Cancel
          </BootstrapButton>
        </ModalFooter>
      </Modal>
      </div>
      {timeOffData &&
  timeOffData.map((tdata, index) => (
    <React.Fragment key={`deletePopup-${index}`}>
      {showDeletePopup[tdata.employeeId] && (
        <div className="delete-popup">
          <button onClick={() => handleDelete(tdata.employeeId)}>Delete</button>
          <button onClick={() => handleClose(tdata.employeeId)}>Close</button>
        </div>
      )}
    </React.Fragment>
  ))}
    </>
  );
};

export default Buttons;
